import React, { Component } from "react";
import UploadForm from "./components/UploadForm";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formCount: 1
    };
  }

  formHandler = e => {
    e.preventDefault();
    this.setState({
      formCount: this.state.formCount + 1
    });
  };

  render() {
    let forms = [];
    for (let i = 0; i < this.state.formCount; i++) {
      forms.push(<UploadForm formHandler={this.formHandler} key={i} />);
    }
    return <div className="container">{forms}</div>;
  }
}

export default App;
